import {required} from "vuelidate/lib/validators";


export default {
  middleware: 'auth',
  auth: 'guest',
  name: 'ForgetPassword',
  components: {},
  data() {
    return {
      response:{
        msg:'',
        variant:''
      },
      formData: {
        email: '',
      },
      errors: null,
    }
  },
  validations: {
    formData: {
      email: {
        required,
        serverError(value) {
          if (value === '') return true
          return !(this.errors != null && this.errors.email && value == this.errors.email.value)
        }
      },
    }
  },
  head() {
    return {
      title: 'Forget Password',
      meta: [
        {
          name: 'description',
          content: 'sign in with Google or Facebook or can create an account'
        },
        {
          name: 'keywords',
          content: 'sign in, login, google, facebook, account'
        }
      ]
    }
  },
  mounted() {
  },
  methods: {

    onReset(evt) {
      evt.preventDefault()
      // Reset our formData values
      this.formData.email = ''

    },

    async onSubmit(evt) {
      this.$v.$reset()
      this.errors = null
      evt.preventDefault()
      this.$v.formData.$touch()
      if (this.$v.formData.$invalid) {
        console.log("vuelidate error")
      } else {
        this.$axios.post('user/forget-password', this.formData).then(async (res) => {
          this.response = res.data
        }).catch(error => {
          if (error.response.data.errors) {
            this.errors = error.response.data.errors
          }
          console.log(error)
        })
      }
    }
  },
}
